import React, { useState, useEffect, createContext, useCallback, useMemo } from 'react';
import { toggleTheme } from 'src/state/ducks/ui';
import StatisticsBanner from 'src/components/StatisticsBanner';
import BestJobsTwo from './BestJobsTwo';
import HappyClients from './HappyClients';
import RegisterAs from './RegisterAs';
import HeroBannerThree from './HeroBannerThree';
import SearchByCategory from './SearchByCategory';
import ControlBusiness from './ControlBusiness';
import DownloadAppThree from './DownloadAppThree';
import RecentResumeThree from './RecentResumeThree';
import PricingPlan from './PricingPlan';
import AppBanner from 'src/components/AppBanner';
import axios from 'axios';
import { Spinner } from 'reactstrap';


export const Context = createContext();

const HomeThree = () => {
    const token = localStorage.getItem('token');
    const [user, setUser] = useState({});
    const [currentPosts, setCurrentPosts] = useState([]);
    const [tabNumber, setTabNumber] = useState('1');

    const func = useCallback((services) => setCurrentPosts(services), [currentPosts]);
    const activeTab = useCallback((n) => setTabNumber(n), [user]);

    useEffect(() => {
        function fetchData() {
            axios.get(`https://www.togedoortestgo.site/graphql/accounts/${localStorage.id}`).then((res) => {
                setUser(res.data);
            });
        }
        fetchData();
    }, []);

    const objValues = useMemo(
        () => ({
            func,
            currentPosts,
        }),
        [currentPosts]
    );

    return token ? (
        <>
            <Context.Provider value={objValues}>
                {/* <Loading/> */}

                {/* <HeroBannerThree /> */}
                <AppBanner user={user} />
                <SearchByCategory tabNumber={tabNumber} />
                <BestJobsTwo props={activeTab} /> {/* ==> BestJobItems ==> BestJobsCard */}
                {/* <StatisticsBanner banner3 />
                <ControlBusiness />
                <DownloadAppThree />
                <HappyClients /> */}
                {/* <PricingPlan pricing3 /> */}
                {/* <RegisterAs /> */}
                <RecentResumeThree />
            </Context.Provider>
        </>
    ) : (
        <>
            <Context.Provider value={objValues}>
                <HeroBannerThree />
                <BestJobsTwo props={activeTab} />
                {/* <StatisticsBanner banner3 />
                <ControlBusiness />
                <DownloadAppThree />
                <HappyClients /> */}
                {/* <PricingPlan pricing3 /> */}
                {/* <RegisterAs /> */}
                <RecentResumeThree />
            </Context.Provider>
        </>
    );
};

export default HomeThree;
