import React, { useState, useEffect, useRef, useContext, useCallback, useMemo } from 'react';
import { Col, Container, Form, Row, FormGroup, Input, Button } from 'reactstrap';
import Select from 'react-dropdown-select';
import axios from 'axios';
import { Context } from './../../LayoutOne/index';
import ReactDOM from 'react-dom';

const instance = axios.create({
  baseURL: 'https://www.togedoortestgo.site/graphql',
});

function concatWithDash(string) {
  return string.split(' ').join('-');
}
const SearchByCategory = ({ tabNumber }) => {
  const [posts, setPosts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [categoryName, setCategoryName] = useState('Agriculture & Natural Resources');
  const [subCategories, setSubCategories] = useState([]);
  const [subCategoryID, setSubCategoryID] = useState('');
  const [subCategoryName, setSubCategoryName] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  const inputRef = useRef(null);
  const selectCategoryRef = useRef(categoryName);
  const selectSubCategoryRef = useRef(subCategoryName);
  const formRef = useRef();

  const context = useContext(Context);

  const postsPerPage = 10;
  const currentPage = 1;

  //=* OnChanges
  const onPostName = (e) => setSearchTerm(e.target.value);

  const onCategoryName = (e) => {
    setCategoryName(e[0].value);
  };

  const onSubCategory = (e) => {
    const arr = subCategories.filter((category) => {
      setSubCategoryName(e[0].label);
      return category.name === e[0].label;
    });
    setSubCategoryID(arr[0]._id);
    setSearchTerm(searchTerm);
  };

  /** set posts with @callback useFetch */
  const useFetch = useCallback(async (url, callbacks, categoryName) => {
    try {
      const { data } = await instance.get(categoryName ? `categories/name/${categoryName}` : url);

      callbacks.forEach((callback) => callback(categoryName ? data.subcategories : data));
    } catch (e) {
      throw e;
    }
  }, []);

  useEffect(() => {
    useFetch('services', [setPosts, setSearchResults]);
    useFetch('categories', [setCategories]);
  }, []);

  useEffect(() => {
    useFetch('categoryName', [setSubCategories], concatWithDash(categoryName));
  }, [categoryName]);

  useEffect(() => {
    const result = posts.filter(
      (service) =>
        service.JobTitle.toLowerCase().includes(searchTerm.toLowerCase()) &&
        service.categoryName.includes(categoryName) &&
        service.subCategoryID.includes(subCategoryID)
    );
    setSearchResults(result);
  }, [searchTerm, categoryName, subCategoryID]);

  //TODO Check sortedByRating & sortedByDate
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;

  const sortedByRating = searchResults
    .sort((a, b) => (a.avgRating < b.avgRating ? 1 : -1))
    .slice(indexOfFirstPost, indexOfLastPost);

  const sortedByDate = searchResults
    .sort((a, b) => (a.createdAt < b.createdAt ? 1 : -1))
    .slice(indexOfFirstPost, indexOfLastPost);

  useEffect(() => {
    if (tabNumber === '1') {
      context.func(sortedByDate);
    } else {
      context.func(sortedByRating);
      sortedByRating.filter((item) => item.avgRating !== 'new post');
    }
  }, [searchResults, tabNumber]);
  const selectSubCategory = (
    <Select
      ref={selectSubCategoryRef}
      className='border-left-0 border-top-0 border-bottom-0 text-muted form-control'
      placeholder='Sub category'
      name='subCategoryID'
      onChange={onSubCategory}
      options={subCategories.map((subCategory) => ({
        value: subCategory._id,
        label: subCategory.name,
      }))}
    />
  );

  const selectCategory = (
    <Select
      ref={selectCategoryRef}
      className='border-left-0 border-top-0 border-bottom-0 text-muted form-control'
      placeholder='Category'
      name='categoryName'
      onChange={onCategoryName}
      options={categories.map((category) => ({
        value: category.name.split(' ').join('-'),
        label: category.name,
      }))}
    />
  );

  const input = (
    <Input
      // innerRef={inputRef}
      value={searchTerm}
      ref={inputRef}
      type='text'
      name='tag'
      className='border-0'
      placeholder='Search project...'
      onChange={onPostName}
    />
  );

  const toggleReset = () => {
    inputRef.current.value = '';
    setSearchResults(posts);

    const parentDivOfCategory = selectCategoryRef.current.valueOf().getSelectRef().parentElement;
    ReactDOM.render(selectCategory, parentDivOfCategory);

    const parentDivOfSubCategory = selectSubCategoryRef.current.valueOf().getSelectRef().parentElement;
    ReactDOM.render(selectSubCategory, parentDivOfSubCategory);

  };

  return (
    <Container className='searchby-category mt-6 mt-md-13'>
      <Row>
        <Col id='renderCol' lg={12}>
          <Form innerRef={formRef} className='border pl-1'>
            <Row form>
              <Col lg={4} className='pr-0'>
                <FormGroup className='mb-0 py-1 pl-2'>{selectCategory}</FormGroup>
              </Col>
              <Col lg={4} className='p-0'>
                <FormGroup className='mb-0 py-1'>{selectSubCategory}</FormGroup>
              </Col>
              <Col lg={4} className=' d-flex justify-content-between'>
                <FormGroup id='inputRender' className='mb-0 py-1 keyword'>
                  {input}
                </FormGroup>
                <Button onClick={toggleReset}>
                  <sub>
                    <strong>
                      <b>
                        <span style={{ fontSize: '30px' }}>&times;</span>
                      </b>
                    </strong>
                  </sub>
                </Button>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default SearchByCategory;
