import React, { useEffect, useState, useContext, useCallback } from 'react';
import { Col, Row, Button } from 'reactstrap';
import BestJobsCard from './BestJobsCard';
import { Context } from '../../../LayoutOne/index';
import { Link } from 'react-router-dom';

const BestJobItems = () => {
    const context = useContext(Context);
    const [currentPosts, setCurrentPosts] = useState([]);

    useEffect(() => setCurrentPosts(context.currentPosts), [context]);

    return (
        <>
            <Row>
                {currentPosts &&
                    currentPosts.map((item, i) => (
                        <Col md={12} lg={6} key={i + item}>
                            <BestJobsCard {...item} />
                        </Col>
                    ))}
                <Col lg={12} className='text-center mt-4 mt-md-7'>
                    <Link to='job'>
                        <Button className='rounded-pill gradient-button border-0'>View all projects</Button>
                    </Link>
                </Col>
            </Row>
        </>
    );
};

export default BestJobItems;
